.skills{
    &__content-wrapper{
        display: flex;
        padding: 20px;
        gap:20px;
        margin-top: 100px;

        &__inner-content{
            flex:1;
            display: flex;
            flex-direction: column;
            align-items: center;

            &__category-text{
                display: inline-block;
                color:var(--yellow-theme-main-color);
                font-size:28px;
                font-weight:700;
                line-height:1rem;
                letter-spacing: 0.5px;
                position: relative;
                text-transform: uppercase;
                padding: 5px 10px;
                border-bottom: 2px solid var(--yellow-theme-main-color);
            }
            &__category-text::before,&__category-text::after{
                position: absolute;
                content:'' ;
                width:2px;
                height: 5px;
                background-color: var(--yellow-theme-main-color);
                bottom:-1px;
            }
            &__category-text::before{
                left:0
            }
            &__category-text::after
{
    right:0;
} 
&__progressbar-cointainer {
    width: 100%;
}
.progressbar-wrapper{
    padding:50px 0px 0px 0px;
    p{
        font-size:2.0rem;
        margin:0 0 10px;
        font-weight:500;
        text-transform: uppercase;
        color:var(--yellow-theme-sub-text-color);
    }
}
}
}
}


@media only screen and(max-width:768px){
   .skills{
    // margin-top: 500px;
    &__content-wrapper{
          margin-top: 220px;
// 
        flex-direction:column;
    }
   }
   .skills__content-wrapper__inner-content_progressbar-cointainer{
    margin-bottom:20px;
   }
}