.home{
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    &__text-wrapper{
        display: flex;
        justify-content: center;
        // align-content: center;
        // flex-direction: column;
        h1{
            color: var(--yellow-theme-main-color);
            margin:0px;
            font-weight: 500;
            font-size: 8rem;
            line-height: 10rem;
            position: relative;
            overflow: hidden;
            white-space: nowrap;
            animation: typingEffectAnimation 2.5s steps(30,end);


        }
    }
}
@media only screen and (max-width:550px){
  .home__text-wrapper h1{
    font-size:3rem;
    line-height:5rem;
  }  

}
@media only screen and (min-width:551px) and (max-width:990px){
    .home__text-wrapper h1{
        font-size:4rem;
        line-height:6rem;
      }  
}
.btn{
    width: max-content;
    display: inline-block;
    font-size:large;
    color:black;
    margin-right: 8px;
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border:1px solid var(--yellow-theme-main-color);
    transition:  hsl(203, 80%, 67%);

}
.btn:hover{
    background: hsl(203, 80%, 67%);
    color: hsl(0, 4%, 5%);
}
.btn-primary{
    background:  hsl(203, 80%, 67%);
    color:#fff;
}


@media only screen and  (min-width:768px) and (max-width:990px){
    .home__text-wrapper h1{
        font-size:6rem;
        line-height:8rem;
      }  
}
@keyframes typingEffectAnimation {
    from{width:0;}
    to{width:100%;}
}